
.swiper-container,
.swiper-wrapper,
.swiper-slide {
  width: 100%;
  height: 100%;
}
.swiper-container {
  position: relative;
}
.swiper-slide {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  color: #eee;
}
.swiper-pagination {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 10px;
  text-align: center;
  font-size: 0;
}
.swiper-pagination span {
  display: inline-block;
  width: 8px;
  height: 8px;
  margin: 0 3px;
  background: rgba(151, 151, 151, 0.25);
  border-radius: 50%;
  overflow: hidden;
}
.swiper-pagination .swiper-pagination-bullet-active {
  background: rgba(214, 60, 23, 0.5);
}
.swiper-scrollbar {
  position: absolute;
  right: 3px;
  top: 0.5%;
  width: 3px;
  height: 99%;
  border-radius: 5px;
  background: rgba(255, 255, 255, 0.2);
  overflow: hidden;
}
.swiper-scrollbar .swiper-scrollbar-drag {
  width: 100%;
  height: 100%;
  position: relative;
  border-radius: 5px;
  overflow: hidden;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.5);
}



